
import customerConfiguration from "./customer/customer-configuration"

class Customer{
    ID:number
    name:string
    discount:number

    constructor(CustomerData:customerConfiguration){
        this.ID = CustomerData.ID
        this.name = CustomerData.name
        this.discount = CustomerData.discount
    }

    getID(){
        console.log(this.ID)
    }

    getName(){
        console.log(this.name)
    }

    getDiscount(){
        console.log(this.discount)
    }

    setDiscount(discountValue:number){
        this.discount = discountValue
    }

    toString(){
        console.log(`${this.name} (${this.ID})`)
    }
}

let customerObject:customerConfiguration = {ID:1,name:"Visshnnu",discount:10}

let customer = new Customer(customerObject)
customer.getID()
customer.getName()
customer.getDiscount()
customer.toString()